<style>
	.bg-img {
		position: fixed;
		inset: 0;

		z-index: -1;
		user-select: none;
	}

	.bg-img::before {
		position: fixed;
		content: "";
		z-index: -1;
		inset: 0;
		mix-blend-mode: screen;
		background: var(--grain), linear-gradient(#121212 30%, #242424 100%) no-repeat;
		background-size: 150px, 100%, cover;
		background-blend-mode: overlay;
		opacity: 1;
	}

	.bg-img > svg {
		object-fit: cover;
		min-height: 100%;
		min-width: 100%;
	}

	@keyframes moveGradient {
    0% {
      stop-color: hsl(105, 69%, 40%);
    }
    50% {
      stop-color: hsl(105, 69%, 60%);
    }
    100% {
      stop-color: hsl(105, 69%, 40%);
    }
  }

  #oooscillate-grad stop {
    /* animation: moveGradient 20s infinite; */
  }
</style>